/**
 * 系统管理  公司管理
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>部门管理</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索筛选 -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="搜索：">
        <el-input size="small" v-model="formInline.name" placeholder="输入部门名称"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.code" placeholder="输入部门代码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()">添加</el-button>
      </el-form-item>
    </el-form>
    <!--列表-->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column align="center" type="index" width="60">
      </el-table-column>
      <el-table-column sortable prop="name" label="部门名称" width="150">
      </el-table-column>
      <el-table-column sortable prop="type" label="部门级别" width="100">
      </el-table-column>
      <el-table-column sortable prop="code" label="部门代码" width="100">
      </el-table-column>
      <el-table-column sortable prop="parentsName" label="直系上级" width="150">
      </el-table-column>
      <el-table-column sortable prop="leaderName" label="部门负责人" width="120">
      </el-table-column>
      <el-table-column sortable prop="description" label="部门描述" width="200">
      </el-table-column>
      <el-table-column sortable prop="updatedTime" label="修改时间" width="160">
        <template slot-scope="scope">
          <div>{{scope.row.updatedTime|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作" min-width="180">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="deleteDept(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页组件 -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!-- 编辑界面 -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click="closeDialog">
      <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
        <el-form-item label="部门名称" prop="name">
          <el-input size="small" v-model="editForm.name" auto-complete="off" placeholder="请输入部门名称"></el-input>
        </el-form-item>
        <el-form-item label="部门代码" prop="code">
          <el-input size="small" v-model="editForm.code" auto-complete="off" placeholder="请输入部门代码"></el-input>
        </el-form-item>
        <el-form-item label="部门级别" prop="type">
          <el-select size="small" clearable v-model="editForm.type" placeholder="请选择部门级别">
            <el-option label="1" value="1"></el-option>
            <el-option label="2" value="2"></el-option>
            <el-option label="3" value="3"></el-option>
            <el-option label="4" value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="直系上级部门"  prop="parentsId">
          <el-select size="small" clearable v-model="editForm.parentsId" placeholder="请选择直系上级部门">
            <el-option v-for="item in deptList" :key="item.id" :label="item.name"  :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="部门负责人"  prop="leaderId">
          <el-select size="small" clearable v-model="editForm.leaderId" placeholder="请选择部门负责人">
            <el-option v-for="item in userList" :key="item.uid" :label="item.realName"  :value="item.uid"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="部门描述" prop="description">
          <el-input type="textarea" size="small" v-model="editForm.description" auto-complete="off" placeholder="请输入部门描述"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="state">
          <el-switch  v-model="editForm.state" active-value='1' inactive-value='0'  active-color="#13ce66" inactive-color="#ff4949"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog">取消</el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { userList, getDeptList, getDeptListWithPage, postDept } from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      editFormVisible: false, //控制编辑页面显示与隐藏
      title: '添加',
      editForm: {
        id: '',
        name: '',
        code: '',
        type: '',
        parentsId: '',
        leaderId: '',
        description: '',
        state: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // rules表单验证
      rules: {
        name: [
          { required: true, message: '请输入部门名称', trigger: 'blur' }
        ],
        code: [{ required: true, message: '请输入部门代码', trigger: 'blur' }],
        type: [{ required: true, message: '请选择部门级别', trigger: 'blur' }]
      },
      formInline: {
        pageNum: 1,
        pageSize: 10,
        name: '',
        code: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 删除部门
      deleteDeptParamer: {
        id: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      tokenparm: {
        usertoken: sessionStorage.getItem('logintoken')
      }, 
       //请求用户数据
      userListParams: {
          pageNum : 1,
          pageSize : 1000,
          usertoken: sessionStorage.getItem('logintoken')
      },
      listData: [], //用户数据
      userList: [], //用户数据
      deptList: [], //部门信息
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */

  /**
   * 创建完毕
   */
  created() {
    this.getdata(this.formInline)
    this.getDeptDownList(this.tokenparm)
    this.getUserDownList(this.userListParams)
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    // 获取公司列表
    getdata(parameter) {
      getDeptListWithPage(parameter)
        .then(res => {
          this.loading = false
          if (res.code != 200) {
            this.$message({
              type: 'info',
              message: res.data
            })
          } else {
            this.listData = res.data.list
            console.log(this.listData)
            // 分页赋值
            this.pageparm.currentPage = this.formInline.pageNum
            this.pageparm.pageSize = this.formInline.pageSize
            this.pageparm.total = res.data.total
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('服务器错误！')
        })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.pageNum = parm.currentPage
      this.formInline.pageSize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 搜索事件
    search() {
      console.log(this.formInline)
      this.getdata(this.formInline)
    },
    //显示编辑界面
    handleEdit: function(index, row) {
      this.editFormVisible = true
      var i = JSON.stringify(row);
        console.log(i)
      if (row != undefined && row != 'undefined') {
        console.log("row.state"+row.state)
        this.title = '修改'
        this.editForm.id = row.id
        this.editForm.name = row.name
        this.editForm.code = row.code
        this.editForm.type = row.type
        this.editForm.leaderId = row.leaderId
        this.editForm.state = row.state
        this.editForm.parentsId = row.parentsId
      } else {
        this.title = '添加'
        this.editForm.id = ''
        this.editForm.name = ''
        this.editForm.code = ''
        this.editForm.type = ''
        this.editForm.leaderId = ''
        this.editForm.state = ''
      }
    },
    // 编辑、增加页面保存方法
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          console.log(this.editForm)
          postDept(this.editForm)
            .then(res => {
              this.editFormVisible = false
              this.loading = false
              if (res.code == 200) {
                this.getdata(this.formInline)
                this.$message({
                  type: 'success',
                  message: '部门信息保存成功！'
                })
              } else {
                this.$message({
                  type: 'info',
                  message: res.data
                })
              }
            })
            .catch(err => {
              this.editFormVisible = false
              this.loading = false
              this.$message.error('部门信息保存失败，请稍后再试！')
            })
        } else {
          return false
        }
      })
    },
    getDeptDownList(param){
      getDeptList(param).then(res =>{
        if(res.code == 200){
          this.deptList = res.data
        }else{
          this.$message.error(res.data)
        }
      }).catch(err => {
        this.$message.error('服务器出错！')
      })
    },
    getUserDownList(param){
      userList(param).then(res =>{
        if(res.code == 200){
          this.userList = res.data.list
        }else{
          this.$message.error(res.data)
        }
      }).catch(err=>{
        this.$message.error('服务器出错！')
      })
    },
    // 删除部门
    deleteDept(index, row) {
      this.$confirm('确定要删除吗?', '信息', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
          this.deleteDeptParamer.id = row.id
          postDept(this.deleteDeptParamer)
            .then(res => {
              if (res.code == 200) {
                this.$message({
                  type: 'success',
                  message: '部门已删除!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.data
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('公司删除失败，请稍后再试！')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 关闭编辑、增加弹出框
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 


