/**
 * 待办事宜
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>待办事宜</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索筛选 -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search">刷新</el-button>
      </el-form-item>
    </el-form>
    <!--列表-->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="title" label="流程标题" width="180">
      </el-table-column>
      <el-table-column sortable prop="applicationReason" label="申请原因" width="180">
      </el-table-column>
      <el-table-column sortable prop="createdName" label="申请人名称" width="180">
      </el-table-column>
      <el-table-column sortable prop="level" label="重要等级" width="100">
      </el-table-column>
      <el-table-column sortable prop="createdTime" label="创建时间" width="200">
        <template slot-scope="scope">
          <div>{{scope.row.createdTime|timestampToTime}}</div>
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" type="primary"  @click="handleEdit(scope.$index, scope.row)">审核</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页组件 -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!-- 编辑界面 -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click="closeDialog">
      <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
        <el-form-item label="流程名" prop="title">
          <el-input size="small" disabled v-model="editForm.title" auto-complete="off" ></el-input>
        </el-form-item>
        <el-form-item label="申请人" prop="createdName">
          <el-input size="small" disabled v-model="editForm.createdName" auto-complete="off" ></el-input>
        </el-form-item>
        <el-form-item label="是否通过" prop="isPass">
          <el-switch v-model="editForm.isPass" nshow active-value="1" inactive-value="0"  active-color="#13ce66" inactive-color="#ff4949"></el-switch>
        </el-form-item>
        <el-form-item label="不通过原因" prop="unPassReason">
          <el-input size="small" type="textarea" v-model="editForm.unPassReason" auto-complete="off" placeholder="请输入原因"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog">取消</el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getTodoList,postProcess } from '../../api/processMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      editFormVisible: false, //控制编辑页面显示与隐藏
      title: '审核流程信息',
      editForm: {
        title:'',
        id:'',
        createdName:'',
        isPass: '',
        unPassReason: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // rules表单验证
      rules: {
        isPass: [
          { required: true, message: '请输入显示名称', trigger: 'blur' }
        ],
        payOpen: [{ required: true, message: '请选择状态', trigger: 'blur' }]
      },
      formInline: {
        pageNum: 1,
        pageSize: 10,
        uid:'',
        usertoken: sessionStorage.getItem('logintoken')
      },
      //类型列表
      typeList: [],
      // 删除部门
      seletedata: {
        id: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      user:{},
      usertokenParamer :{
        uid:'',
        pageNum:'',
        pageSize:'',
        usertoken: sessionStorage.getItem('logintoken')
      },
      userparm: [], //搜索权限
      listData: [], //用户数据
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */

  /**
   * 创建完毕
   */
  created() {
    this.user = JSON.parse(sessionStorage.getItem('userdata'))
    this.formInline.uid = this.user.uid
    this.getdata(this.formInline)
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    // 获取待办事宜列表
    getdata(parameter) {
      this.loading = true
      /***
       * 调用接口，注释上面模拟数据 取消下面注释
       */
      getTodoList(parameter)
        .then(res => {
          this.loading = false
          if (res.code == 200) {
            this.listData = res.data.list
            // 分页赋值
            this.pageparm.currentPage = this.formInline.pageNum
            this.pageparm.pageSize = this.formInline.pageSize
            this.pageparm.total = res.data.total
          } else {
            this.$message({
              type: 'info',
              message: res.data
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('数据获取失败，请稍后再试！')
        })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    // 搜索事件
    search() {
      this.getdata(this.formInline)
    },
    //显示编辑界面
    handleEdit: function(index, row) {
      this.editFormVisible = true
      this.editForm.title = row.title
      this.editForm.createdName = row.createdName
      this.editForm.id = row.nodeId
    },
    // 保存审核信息方法
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          postProcess(this.editForm)
            .then(res => {
              this.editFormVisible = false
              this.loading = false
              if (res.code == 200) {
                this.getdata(this.formInline)
                this.$message({
                  type: 'success',
                  message: '审核成功！'
                })
              } else {
                this.$message.error(res.data)
              }
            })
            .catch(err => {
              this.editFormVisible = false
              this.loading = false
              this.$message.error('审核信息失败，请稍后再试！')
            })
        } else {
          return false
        }
      })
    },
    // 关闭编辑、增加弹出框
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 
 


